import * as THREE from 'three';

function scaleModel(geometry, sandpit_edge_length) {
    const target_length = 0.2 * sandpit_edge_length;

    const initial_bbox = new THREE.Box3().setFromObject(geometry);
    let size = new THREE.Vector3();
    initial_bbox.getSize(size);
    let maxDim = Math.max(...size.toArray());
    let scaleFactor = target_length / maxDim;
    geometry.scale.set(geometry.scale.x * scaleFactor, geometry.scale.y * scaleFactor, geometry.scale.z * scaleFactor);

    const newYPos = scaleFactor * (size.y / 2)
    geometry.position.set(geometry.position.x, newYPos, geometry.position.z);
}

function moveModel(geometry, targetPoint, sandpitBbox){
    let currentX = geometry.position.x;
    let currentZ = geometry.position.z;

    geometry.position.x = targetPoint.x;
    geometry.position.z = targetPoint.z;

    let bbox = new THREE.Box3().setFromObject(geometry);

    if(!sandpitBbox.containsBox(bbox)){
        geometry.position.x = currentX;
        geometry.position.z = currentZ;
    }
}

function rotateModel(geometry, rotationDelta){
    geometry.rotation.y += 0.01 * rotationDelta
}

export { scaleModel, moveModel, rotateModel }