import "./styles.css";

import React, { useState, useCallback } from "react"
import { Vector3 } from 'three'
import create from 'zustand'
import { Canvas } from "react-three-fiber";
import Sandpit from "./components/Sandpit";
import RaycastPlane from "./components/RaycastPlane"
import Lights from "./components/Lights";
import Camera from "./components/Camera";
import UI from "./components/UI";
import Models from "./components/Models"

import Beetle from "./components/Models/Beetle"
import Car from "./components/Models/Car"
import Chair from "./components/Models/Chair"
import Fox from "./components/Models/Fox"
import OldBarrel from "./components/Models/OldBarrel"
import Tiger from "./components/Models/Tiger"
import WineBottle from "./components/Models/WineBottle"

import { Environment } from '@react-three/drei'
import { presetsObj } from '@react-three/drei/helpers/environment-assets'


const modelsInitState =   {
  beetle: {model3D: Beetle, thumbnail: "/beetle_thumbnail.jpg", startPos: new Vector3(), visible: true},
  wine_bottle: {model3D: WineBottle, thumbnail: "/wine_bottle_thumbnail.jpg", startPos: new Vector3(), visible: false},
  car: {model3D: Car, thumbnail: "/car_thumbnail.jpg", startPos: new Vector3(), visible: false},
  chair: {model3D: Chair, thumbnail: "/chair_thumbnail.jpg",  startPos: new Vector3(), visible: false},
  fox: {model3D: Fox, thumbnail: "/fox_thumbnail.jpg", startPos: new Vector3(), visible: false},
  old_barrel: {model3D: OldBarrel, thumbnail: "/old_barrel_thumbnail.jpg",  startPos: new Vector3(), visible: false},
  tiger: {model3D: Tiger, thumbnail: "/tiger_thumbnail.jpg",  startPos: new Vector3(), visible: false}
}

const useStore = create(set => ({
  models: modelsInitState,
  setModelVisible: (modelName) => set(state => {
                                              state.models[modelName].visible = true;
                                              return {models: state.models}
                                  }),
  setModelHidden: (modelName) => set(state => {
                                              state.models[modelName].visible = false;
                                              return {models: state.models}
                                  })
}))

function App() {
  const sandpit_edge_length = 100

  const camera_height = 100
  const camera_offset = 40
  const camera_down_angle = -0.4*Math.PI

  const [draggedModel, setDraggedModel] = useState(null);
  const [rotatedModel, setRotatedModel] = useState(null);
  const [rotateMovement, setRotateMovement] = useState(null);
  const [mouseBaseIntersect, setmouseBaseIntersect] = useState(new Vector3());

  const {models, setModelVisible, setModelHidden} = useStore()

  function updateDraggedModel(modelId) {
    setDraggedModel(modelId)
  }

  function updateRotatedModel(modelId) {
    setRotatedModel(modelId)
  }

  const onPointerUp = useCallback(
    e => { 
      setDraggedModel(null); 
      setRotatedModel(null); 
      setRotateMovement(null); 
    },
    []
  );

  const onPointerMove = useCallback(
    e => {
      e.stopPropagation();
      let baseIntersection = e.intersections[0].point
      setmouseBaseIntersect(baseIntersection);
      setRotateMovement(e.movementX)
    },
    []
  );


  const presets = Object.keys(presetsObj)
  const preset =  presets[4]

  return (
    <>
      <UI models={models} onAddModel={setModelVisible} onRemoveModel={setModelHidden}/>
      <Canvas onPointerUp={e => onPointerUp(e)} 
              onContextMenu={(e)=> e.preventDefault()}>
        <Camera position={[0, camera_height, camera_offset]} rotation={[camera_down_angle, 0, 0]}/>
        <RaycastPlane rotation={[Math.PI / 2, 0, 0]} onPointerMove={e => onPointerMove(e)}/>
        <Sandpit sandpit_edge_length={sandpit_edge_length} />
        <Lights />
        <React.Suspense fallback={null}>
          <Environment preset={preset} background={true}/>
        </React.Suspense>
        <Models models={models} 
                mouseBaseIntersect={mouseBaseIntersect} 
                draggedModel={draggedModel}
                updateDraggedModel={updateDraggedModel} 
                rotatedModel={rotatedModel}
                updateRotatedModel={updateRotatedModel} 
                rotateMovement={rotateMovement}
                sandpit_edge_length={sandpit_edge_length}/>
      </Canvas>
    </>
  );
}

export default App;