import React, { useRef, useMemo } from "react"
import { TextureLoader } from 'three'
import Edge from "./Edge";
import SandPlane from "./SandPlane";

export default (props) => {
  const group = useRef();

  const half_edge_length = props.sandpit_edge_length / 2
  const edge_width = 2

  const woodTexture = useMemo(() => new TextureLoader().load("/wood.png"), [props.sandpit_edge_length])

  return (
    <>
      <group ref={group}>
        <Edge {...props} position={[0, 0, -half_edge_length]} rotation={[0, -Math.PI / 2, 0]} edge_width={edge_width} texture={woodTexture}/>
        <Edge {...props} position={[0, 0, half_edge_length]} rotation={[0, -Math.PI / 2, 0]} edge_width={edge_width} texture={woodTexture}/>
        <Edge {...props} position={[-(half_edge_length - (edge_width/2)), 0, 0]} rotation={[0, 0, 0]} edge_width={edge_width} texture={woodTexture}/>
        <Edge {...props} position={[half_edge_length - (edge_width/2), 0, 0]} rotation={[0, 0, 0]} edge_width={edge_width} texture={woodTexture}/>
      </group>
      <SandPlane {...props} rotation={[Math.PI / 2, 0, 0]} />
    </>
  )
};
