import React, { useState } from "react";
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';

export default (props) => {
  const [highlighted, setHighlighted] = useState("");

  const handleMouseEnter = (key) => {
    setHighlighted(key)
  };

  const handleMouseLeave = () => {
    setHighlighted("")
  };

  return (
    <div className="model-menu">
      {
        Object.entries(props.models).map((entry) => {
          const key = entry[0]
          const model = entry[1]
          return <Card className={model.visible ? "visible-card" : "hidden-card"}
                      key={key} 
                      border={(key == highlighted) ? "info" : "light"}
                      onMouseEnter={(e) => handleMouseEnter(key)}
                      onMouseLeave={(e) => handleMouseLeave()}
                  >
                  <Card.Img src={model.thumbnail} alt={key} />
                  <Card.ImgOverlay>
                    {model.visible ?
                      <Button variant="danger" size="sm" className="model-button" onClick={(e) => props.onRemoveModel(key)}>-</Button> :
                      <Button variant="success" size="sm" className="model-button" onClick={(e) => props.onAddModel(key)}>+</Button>
                    }
                  </Card.ImgOverlay>
                </Card>
        })
      }
    </div>
  );
}