import React, { useRef } from "react";

export default (props) => {
  const mesh = useRef();

  return (
    <mesh
      {...props}
      ref={mesh}>
      <boxBufferGeometry args={[props.edge_width, props.edge_width, props.sandpit_edge_length]} />
      <meshBasicMaterial map={props.texture}/>
    </mesh>
  )
};