import React, { Suspense, useMemo } from "react"
import * as THREE from 'three'

export default (props) => {

  // bounding box for sandpit, to make sure models cannot move outside this box
  const sandpitBbox = useMemo(() => {
    const half_edge_length = props.sandpit_edge_length / 2
    const minBounds = new THREE.Vector3(-half_edge_length, -10, -half_edge_length);
    const maxBounds = new THREE.Vector3(half_edge_length, 1000, half_edge_length);
    return new THREE.Box3(minBounds, maxBounds);
  })

  return (
    <Suspense fallback={null}>
      {
        Object.entries(props.models).filter(entry => entry[1].visible).map((entry) => {
          let key = entry[0]
          let modelInfo = entry[1]
          let Model3D = modelInfo.model3D
          return <Model3D {...props} key={key} name={key} 
                          startPos={modelInfo.startPos} 
                          currentlyDragged={key == props.draggedModel} 
                          currentlyRotated={key == props.rotatedModel} 
                          sandpitBbox={sandpitBbox}/>
        })
      }
    </Suspense>
  )
};
