import React, { useRef, useMemo, useCallback } from "react"
import { TextureLoader, DoubleSide } from 'three'

export default (props) => {
  const mesh = useRef();

  const sandTexture = useMemo(() => new TextureLoader().load("/sand.jpg"), [props.sandpit_edge_length])
  const numSegments = 32

  return (
    <mesh
      {...props}
      ref={mesh}>
      <planeGeometry args={[1000, 1000, numSegments, numSegments]} />
      <meshPhongMaterial transparent={true} opacity={0.0} side={DoubleSide}/>
    </mesh>
  )
};